 footer{
    margin-top: 40px;
    padding-left: 260px;
}



.footer-inner a{
    font-family: 'Montserrat', sans-serif;
    color: #121111;
    font-size: 100px;
}

.footer-inner{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.footer-left{
    width: 50%;
    font-size: 30px;
}

.footer-right{
    width: 50%;
    text-align: right;
}

.footer-right ul li{
    display: inline-block;
}

.footer-right ul li a{
    opacity: 0.6;
    font-size: 20px;
    text-transform: uppercase;
    transition: all 0.3s cubic-bezier(0.7, 0, 0.1, 0.5);
}

.footer-right ul li a:hover{
    opacity: 1;
    transition: all 0.3s cubic-bezier(0.7, 0, 0.1, 0.5);
}

.footer-right ul li + li a{
    border-left: 1px solid #110e0e;
    padding-left: 6px;
    margin-left: 6px;
}

.copyright {
    text-align: center;
    color: #999999;
    font-size: 20px;
 }