@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300&family=Pathway+Extreme:wght@500&display=swap');

.content{
    display: flex;
    align-items: left;
    justify-content: center;
    height: 100vh;
    width: 46vw;
    margin-left: 46px;
    margin-right: 50px;
    flex-direction: column;
}
.content h3{
    font-size: 50px;
    color: rgb(15, 15, 15);
    font-family: 'Pathway Extreme', sans-serif;
 

}
h3 div{
    overflow: hidden;
    margin-top: -40px;
}
.content p{
    font-size: 15px;
    width: 80%;
    font-weight: 200;
    color: #0e0d0d;
    font-family: 'Pathway Extreme', sans-serif;
}
.content button{
    width: 150px;
    padding: 15px 15px;
    margin-top: 20px;
    font-family: sans-serif;
    text-transform: capitalize;
    font-size: 15px;
    border: none;
    background-color: #0c0c0c;
    border-radius: 30px;
    color: rgb(252, 247, 247);
}


button a{
    text-decoration: none;
    color: aliceblue;
}